<template>
  <div>
    <!-- alert -->
    <v-alert
      class="alert"
      outlined
      type="success"
      text
      v-show="showSuccessDialog"
      transition="scroll-y-transition"
    >
      
    </v-alert>
    <!-- alert -->
    <v-alert
      class="alert"
      outlined
      type="warning"
      text
      v-show="showFailDialog"
      transition="scroll-y-transition"
    >
      {{ msg }}
    </v-alert>
    <v-container class="pl-16 pr-16">
      <form class="pa-12 grey lighten-5 mt-8">
        <v-text-field v-model="couponInfo.name" label=""></v-text-field>
        <v-select
          :items="typeList"
          v-model="couponInfo.type"
          label=""
        ></v-select>
        <v-text-field
          v-show="couponInfo.type === 'DISCOUNT'"
          v-model="meta.discount"
          label="0  1"
        ></v-text-field>
        <v-text-field
          v-show="couponInfo.type === 'CUT_DOWN'"
          v-model="meta.threshold"
          label=""
        ></v-text-field>
        <v-text-field
          v-show="couponInfo.type === 'CUT_DOWN'"
          v-model="meta.cutDown"
          label=""
        ></v-text-field>
        <v-select
          :items="scopeList"
          v-model="couponInfo.scope"
          label=""
        ></v-select>
        <v-text-field
          v-show="couponInfo.scope === 'SINGLE'"
          v-model="meta.teacherId"
          label="ID"
        ></v-text-field>
        <v-text-field
          v-show="couponInfo.scope === 'TEACHER'"
          v-model="meta.teacherId"
          label="ID"
        ></v-text-field>
        <v-text-field
          v-show="couponInfo.scope === 'SCHOOL'"
          v-model="meta.teacherId"
          label="ID"
        ></v-text-field>
        <v-text-field
          v-model="couponInfo.description"
          label=""
        ></v-text-field>
        <v-text-field
          v-model="couponInfo.startTime"
          label=""
          messages="YYYY-MM-DD HH:MM:SS"
        ></v-text-field>
        <v-text-field
          v-model="couponInfo.endTime"
          label=""
          messages="YYYY-MM-DD HH:MM:SS"
        ></v-text-field>
        <v-switch
          v-model="couponInfo.sharable"
          :label="`: ${couponInfo.sharable.toString()}`"
        ></v-switch>
        <v-btn class="ml-0 mt-8 info" @click="submit">
          
        </v-btn>
      </form>
    </v-container>
  </div>
</template>

<script>
import { createCoupon } from "@/api/coupon";

export default {
  name: "CouponCreate",

  data() {
    return {
      typeList_: ["CUT_DOWN", "DISCOUNT"],
      typeList: ["", ""],
      scopeList_: ["SINGLE", "TEACHER", "SCHOOL", "COMMON"],
      scopeList: [
        "",
        "",
        "",
        ""
      ],
      couponInfo: {
        type: "", // "DISCOUNT"
        scope: "", // "TEACHER" "COMMON"
        name: "",
        description: "",
        metadata: "",
        startTime: "",
        endTime: "",
        sharable: false
      },
      meta: {
        threshold: 0,
        cutDown: 0,
        discount: 0,
        courseId: "",
        teacherId: "",
        schoolId: ""
      },

      showSuccessDialog: false,
      showFailDialog: false,
      msg: ""
    };
  },

  methods: {
    submit() {
      const payload = {
        ...this.couponInfo,
        type: this.typeList_[this.typeList.indexOf(this.couponInfo.type)],
        scope: this.scopeList_[this.scopeList.indexOf(this.couponInfo.scope)],
        metadata: JSON.stringify(this.meta)
      };
      console.log(payload);
      createCoupon(payload).then(res => {
        console.log(res);
        if (res.code === 1) {
          this.showSuccessDialog = true;
          setTimeout(() => {
            this.showSuccessDialog = false;
            this.$router.go(-1);
          }, 1000);
        } else {
          this.showFailDialog = true;
          this.msg = res.msg;
          setTimeout(() => {
            this.showFailDialog = false;
          }, 1000);
        }
      });
    }
  }
};
</script>

<style scoped>
.alert {
  position: fixed;
  left: 50%;
  top: 100px;
  z-index: 999;
}
</style>
